<template>
<div>
    <v2-datepicker v-model="currentValue" style="width:100%;"></v2-datepicker>
</div>
</template>
<script>
import util from "../utils/util.js";
export default {
  name: "FyInputDate",
  componentName: "FyInputDate",
  mixins: [util], //这些混入实例对象可以像正常的实例对象一样包含选项（可以直接this.*使用混入对象中的方法）
  inject: {
    fyForm: {
      default: ""
    },
    fyFormItem: {
      default: ""
    }
  },
  data() {
    return {
      currentValue:
        this.value === undefined || this.value === null ? null : new Date(this.value)
    };
  },
  props: {
    value: [String, Number],
    type: {
      type: String,
      default: "text"
    },
    placeholder: {
      type: String,
      default: ""
    }
  },
  watch: {
    //在input子组件中不能直接改变在外部双向绑定的value
    value(value) {
      this.setCurrentValue(value);
    },
    currentValue(value) {
      this.$emit(
        "input",
        this.formatDate(value)
      );
      this.dispatch("FyFormItem", "el.form.change", [this.formatDate(value)]);
    }
  },
  mounted() {
    this.dispatch("FyFormItem", "formItem.addChildVc", [this]); //发送添加formitem对象的信息
  },
  methods: {
    //date组件的清空还有问题
    resetField() {
      //this.$emit("input", '');
    },
    setCurrentValue(value) {
      if (value === this.formatDate(this.currentValue))
        return; // 如果新旧值一致直接返回
      this.currentValue = value == '' ? null: new Date(value); // 改变当前值
      // this.$emit("input", value); // 触发 input 事件
      // this.$emit("change", value); // 触发 change 事件
      //this.dispatch("FyFormItem", "el.form.change", [value]); // 向父级的派发 el.form.change 事件
    },
    handleFocus(event) {
      const value = event.target.value;
      this.dispatch("FyFormItem", "el.form.focus", [this.formatDate(value)]);
    },
    handleInput(event) {
      const value = event.target.value;
      this.$emit("input", this.formatDate(value));
      this.dispatch("FyFormItem", "el.form.change", [this.formatDate(value)]);
    },
    //Change触发事件还有问题
    handleChange(event) {
      const value = event.target.value;
      this.$emit("change", this.formatDate(value));
      this.dispatch("FyFormItem", "el.form.change", [this.formatDate(value)]);
    },
    handleBlur(event) {
      const value = event.target.value;
      this.dispatch("FyFormItem", "el.form.blur", [this.formatDate(value)]);
    },
    //Thu Mar 19 2015 12:00:00 GMT+0800 (中国标准时间)  >>>  2014-11-12
    formatDate(date) {
      if(date){
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        var d = date.getDate();
        d = d < 10 ? "0" + d : d;
        return y + "-" + m + "-" + d;
      }else{
        return '';
      }
    }
  }
};
</script>
<style>
textarea.fy-input {
  resize: vertical;
  padding: 5px 15px;
  sline-height: 1.5;
  box-sizing: border-box;
  width: 100%;
  font-size: inherit;
  color: #606266;
  background-color: #fff;
  background-image: none;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
input.fy-input {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 30px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
</style>



