            },
          }
      },
      mounted : function() {
        var url = 'http://192.168.11.245:18080/page_field/fieldAdd'
        var params = {}
        //var response = getApiJson(url, params);
        //debugger

       // response.then(function(resp) {
            
            //之所以这里拿到了userInfo，那是因为上面我们在3秒之后
            //resolve了真正的userInfo对象

           // console.log('resp:' + resp.rcode)
        //});
        

        this.$axios.get(url, {
              　　params: {

                }}
            ).then(
                res => {
                    //console.log('res:' + res);
                }
            ).catch(
                err => {
                    //console.log('err:' + err);
                }
            );
      },
      methods: {
        zidingyi(value) {
          this.fieldInfo.field_name = value;
        }
      },
      components: {
        FyForm,FyFormItem,FyInput,
      }
    }
</script>


